<template>
  <div id="map" />
</template>

<script>
import mapboxgl from "mapbox-gl";
import "mapbox-gl/dist/mapbox-gl.css";
import { onMounted } from "vue";
import { createApp, defineComponent, ref, nextTick } from "vue";
import MyPopup from "@/components/MyPopup.vue";

export default {
    components: {
    },
  setup() {
    const title = ref("Unchanged Popup Title");
    onMounted(() => {
      mapboxgl.accessToken = "pk.eyJ1IjoiY2hyYWRpbCIsImEiOiJja28wdnN0ZWMwZ3V3Mndta2Q2bm81bGtjIn0.Rt-7EwN5ftoTjzPYGhJl4Q";
      const map = new mapboxgl.Map({
        container: "map",
        style: "mapbox://styles/mapbox/light-v9",
      });

      map.on("load", () => {
        // Here we want to load a layer
        map.addSource("pol", {
          type: "geojson",
          data:
            "https://raw.githubusercontent.com/chradil/natl-epics/main/POL.geo.json"
        });
        map.addLayer({
          id: "pol-fill",
          type: "fill",
          source: "pol",
          paint: {
            "fill-color": "green",
          },
        });
        // Here we want to setup the dropdown
        map.on("click", "pol-fill", function (e) {
        console.log(e);
        let country = e.features[0].properties;
          new mapboxgl.Popup()
            .setLngLat(e.lngLat)
            .setHTML('<h1><br><br>' + country.name + '</h1>' + '<br>' +'<img width="90%" style="float:center; display: block; margin-left:auto; margin-right:auto" src="'+ country.image +'">' + '<br>' + country.description + '<p>' + country.author + '<br>' + country.email + '<br>' + country.website + '</p>')            
            .addTo(map);
          const MyNewPopup = defineComponent({
            extends: MyPopup,
            setup() {
              return { title };
            },
          });
          nextTick(() => {
            createApp(MyNewPopup).mount("#popup-content");
          });
        });
      });
    });
    return { title };
  },
};
</script>

